// components/MemoList.tsx

import React, { useState, useRef, useEffect, useMemo } from "react";
import { useRecoilState, useRecoilValue } from "recoil";
import { memoListState, selectedMemoState } from "@/recoil/memo";
import { searchResultsState } from "@/recoil/search";
import { deleteMemoFromDB, updateMemoInDB } from "@/utils/indexedDB";
import styled from "@emotion/styled";

const MemoWrap = styled.ul`
  display: flex;
  flex-direction: column;
  gap: 8px;
  max-height: 300px;
  overflow: auto;
`;

const MemoLi = styled.li`
  padding: 8px;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const MemoList: React.FC = React.memo(() => {
  const [memoList, setMemoList] = useRecoilState(memoListState);
  console.log(memoList);
  const [, setSelectedMemo] = useRecoilState(selectedMemoState);
  const searchResults = useRecoilValue(searchResultsState);

  const [editMode, setEditMode] = useState<number | null>(null);
  const [editedContent, setEditedContent] = useState("");
  const textareaRef = useRef<HTMLTextAreaElement | null>(null);

  useEffect(() => {
    //     textarea   
    if (editMode !== null && textareaRef.current) {
      textareaRef.current.focus();
    }
  }, [editMode]);

  const memoData = useMemo(() => {
    // searchResults   memoList ,   searchResults 

    return searchResults.length > 0 ? searchResults : memoList;
  }, [searchResults, memoList]);

  const handleDeleteMemo = async (memoId: number) => {
    try {
      // IndexedDB  
      await deleteMemoFromDB(memoId);

      // Recoil   (  )
      setMemoList((prevMemos) => prevMemos.filter((m) => m.id !== memoId));

      //   
      setSelectedMemo(null);
    } catch (error) {
      console.error("Error deleting memo:", error);
    }
  };

  const handleEditMemo = (memoId: number, content: string) => {
    setEditMode(memoId);
    setEditedContent(content);
  };

  const handleSaveEdit = async (memoId: number) => {
    try {
      // IndexedDB  
      await updateMemoInDB(memoId, editedContent);

      // Recoil   (  )
      setMemoList((prevMemos) =>
        prevMemos.map((m) =>
          m.id === memoId ? { ...m, content: editedContent } : m
        )
      );

      //   
      setEditMode(null);
      setEditedContent("");
    } catch (error) {
      console.error("Error updating memo:", error);
    }
  };

  return (
    <div>
      <MemoWrap>
        {memoData.map((memo) => (
          <MemoLi key={memo.id}>
            {editMode === memo.id ? (
              <>
                <textarea
                  ref={textareaRef}
                  value={editedContent}
                  onChange={(e) => setEditedContent(e.target.value)}
                />
                <button onClick={() => handleSaveEdit(memo.id)}>Save</button>
              </>
            ) : (
              <>
                <span
                  onClick={() => handleEditMemo(memo.id, memo.content)}
                  style={{ cursor: "pointer", textDecoration: "underline" }}
                >
                  {memo.content}
                </span>{" "}
                <button onClick={() => handleDeleteMemo(memo.id)}>
                  Delete
                </button>
              </>
            )}
          </MemoLi>
        ))}
      </MemoWrap>
    </div>
  );
});

export default MemoList;
