// pages/memo-app.tsx

import React from "react";
import MemoList from "@/components/contents/MemoList";
import AddMemoForm from "@/components/contents/AddMemoForm";
import SearchBar from "@/components/contents/SearchBar";

import { useEffect } from "react";
import { useSetRecoilState } from "recoil";
import { memoListState } from "@/recoil/memo";
import { openDB } from "@/utils/indexedDB";

import styled from "@emotion/styled";
const MemoWrap = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  height: 100vh;
`;
const MemoBox = styled.div`
  min-width: 300px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 12px;
  border: 1px solid #ccc;
  background: linear-gradient(to right, #7d6ef4, #926ff7, #8a86e8);
`;
const MemoTitle = styled.h2`
  font-size: 16px;
  margin-bottom: 16px;
  color: #fff;
  text-align: center;
`;
export interface Memo {
  id: number;
  content: string;
}

const MemoApp: React.FC = () => {
  const setMemoList = useSetRecoilState(memoListState);

  useEffect(() => {
    const fetchDataFromIndexedDB = async () => {
      try {
        const db = await openDB();
        const transaction = db.transaction(["Memos"], "readonly");
        const store = transaction.objectStore("Memos");

        // IDBRequest Promise   
        const request: IDBRequest<any[]> = store.getAll();
        const memos: Memo[] = await new Promise((resolve, reject) => {
          request.onsuccess = (event) => resolve(request.result);
          request.onerror = (event) => reject(request.error);
        });

        setMemoList(memos);
      } catch (error) {
        console.error("Error fetching data from IndexedDB:", error);
      }
    };

    fetchDataFromIndexedDB();
  }, [setMemoList]);

  return (
    <MemoWrap>
      <MemoBox>
        <MemoTitle>Memo</MemoTitle>
        <SearchBar />
        <AddMemoForm />
        <MemoList />
      </MemoBox>
    </MemoWrap>
  );
};

export default MemoApp;
