// components/AddMemoForm.tsx

import React, { useState } from "react";
import { useSetRecoilState } from "recoil";
import { memoListState } from "@/recoil/memo";
import { addMemoToDB } from "@/utils/indexedDB";

const AddMemoForm: React.FC = () => {
  const [content, setContent] = useState("");
  const setMemoList = useSetRecoilState(memoListState);
  const [isAddingMemo, setIsAddingMemo] = useState(false);

  const handleAddMemo = async () => {
    if (content.trim() !== "") {
      try {
        //  IndexedDB 
        await addMemoToDB(content.trim());

        // IndexedDB    Recoil  
        setMemoList((prevMemoList) => [
          ...prevMemoList,
          { id: Date.now(), content: content.trim() },
        ]);
        console.log("Memo list updated");
        //   
        setContent("");
      } catch (error) {
        console.error("Error adding memo:", error);
      }
    }
  };

  const handleKeyPress = async (
    e: React.KeyboardEvent<HTMLTextAreaElement>
  ) => {
    // Check if the pressed key is Enter (key code 13)
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault(); // Prevent newline in the textarea
      if (!isAddingMemo) {
        //      
        setIsAddingMemo(true); //    
        await handleAddMemo(); // Call the function to add a memo
        setIsAddingMemo(false); //     
      }
    }
  };

  return (
    <div>
      <h2>Add Memo</h2>
      <textarea
        value={content}
        onChange={(e) => setContent(e.target.value)}
        onKeyDown={handleKeyPress}
      />
      <button onClick={handleAddMemo}>Add Memo</button>
    </div>
  );
};

export default AddMemoForm;
