// components/SearchBar.tsx

import React, { useEffect } from "react";
import { useRecoilState, useSetRecoilState } from "recoil";
import { searchQueryState, searchResultsState } from "@/recoil/search";
import { searchMemos } from "@/utils/searchUtils";
import { getAllMemosFromDB } from "@/utils/indexedDB";

import styled from "@emotion/styled";

const SearchWrap = styled.div`
  display: flex;
  gap: 4px;
  justify-content: space-between;
`;
const SearchInput = styled.input`
  display: flex;
  width: 100%;
  min-height: 32px;
  padding: 4px 12px;
  font-size: 12px;
`;

const SearchBar: React.FC = () => {
  const [searchQuery, setSearchQuery] = useRecoilState(searchQueryState);
  const setResults = useSetRecoilState(searchResultsState);

  useEffect(() => {
    console.log("Search Query:", searchQuery);
    const fetchMemos = async () => {
      try {
        const memos = await getAllMemosFromDB();
        setResults(searchMemos(memos, searchQuery));
      } catch (error) {
        console.error("Error fetching memos:", error);
      }
    };

    fetchMemos();
  }, [searchQuery, setResults]);

  return (
    <SearchWrap>
      <SearchInput
        type="text"
        value={searchQuery}
        onChange={(e) => setSearchQuery(e.target.value)}
      />
    </SearchWrap>
  );
};

export default SearchBar;
