// utils/indexedDB.ts
import { Memo } from "@/recoil/memo";

const DB_NAME = "MemoDB";
const STORE_NAME = "Memos";
const DB_VERSION = 1;
// utils/indexedDB.ts

export const openDB = (): Promise<IDBDatabase> => {
  return new Promise((resolve, reject) => {
    const request = indexedDB.open(DB_NAME, DB_VERSION);

    request.onerror = (event) => {
      reject((event as any).target.error); //   
    };

    request.onsuccess = (event) => {
      const db = (event.target as any).result;
      resolve(db);
    };

    request.onupgradeneeded = (event) => {
      const db = (event.target as any).result;
      if (!db.objectStoreNames.contains(STORE_NAME)) {
        db.createObjectStore(STORE_NAME, {
          keyPath: "id",
          autoIncrement: true,
        });
      }
    };
  });
};

// utils/indexedDB.ts

export const addMemoToDB = async (content: string): Promise<void> => {
  const db = await openDB();

  return new Promise((resolve, reject) => {
    const transaction = db.transaction([STORE_NAME], "readwrite");
    const store = transaction.objectStore(STORE_NAME);

    const memo = { content };

    const request = store.add(memo);

    request.onerror = (event) => {
      reject((event as any).target.error); //   
    };

    request.onsuccess = () => {
      resolve();
    };
  });
};

// utils/indexedDB.ts

export const deleteMemoFromDB = async (memoId: number): Promise<void> => {
  const db = await openDB();

  return new Promise((resolve, reject) => {
    const transaction = db.transaction(["Memos"], "readwrite");
    const store = transaction.objectStore("Memos");

    const request = store.delete(memoId);

    request.onerror = (event) => {
      reject((event as any).target.error);
    };

    request.onsuccess = () => {
      resolve();
    };
  });
};

// utils/indexedDB.ts
export const updateMemoInDB = async (
  memoId: number,
  newContent: string
): Promise<void> => {
  return new Promise((resolve, reject) => {
    const request = indexedDB.open(DB_NAME, DB_VERSION);

    request.onerror = (event: any) => {
      reject(event.target.error);
    };

    request.onsuccess = (event: any) => {
      const db = event.target.result;
      const transaction = db.transaction([STORE_NAME], "readwrite");
      const store = transaction.objectStore(STORE_NAME);

      const getRequest = store.get(memoId);

      getRequest.onerror = (event: any) => {
        reject(event.target.error);
      };

      getRequest.onsuccess = (event: any) => {
        const existingMemo = event.target.result;

        if (existingMemo) {
          // Delete existing memo
          const deleteRequest = store.delete(memoId);

          deleteRequest.onerror = (event: any) => {
            reject(event.target.error);
          };

          deleteRequest.onsuccess = () => {
            // Add updated memo
            const addRequest = store.add({
              ...existingMemo,
              content: newContent,
            });

            addRequest.onerror = (event: any) => {
              reject(event.target.error);
            };
          };
        } else {
          reject(new Error("Memo not found"));
        }
      };

      //    
      transaction.oncomplete = () => {
        resolve();
      };
    };
  });
};

