export const initializeDB = async (): Promise<IDBDatabase> => {
  return new Promise(async (resolve, reject) => {
    const existingDB = await indexedDB.databases();
    if (existingDB.find((db) => db.name === DB_NAME)) {
      await indexedDB.deleteDatabase(DB_NAME);
    }

    const request = indexedDB.open(DB_NAME, DB_VERSION);

    request.onerror = (event: any) => {
      reject(event.target.error);
    };

    request.onsuccess = (event: any) => {
      const db = event.target.result;
      resolve(db);
    };

    request.onupgradeneeded = (event: any) => {
      const db = event.target.result;

      //    
      const existingStore = db.objectStoreNames.contains(STORE_NAME);
      if (existingStore) {
        db.deleteObjectStore(STORE_NAME);
      }

      //    

      //     
      // ...

      //    onsuccess  
    };
  });
};

export const deleteAndRecreateDB = async (): Promise<void> => {
  const db = await openDB();
  db.close();

  return new Promise((resolve, reject) => {
    const deleteRequest = indexedDB.deleteDatabase(DB_NAME);

    deleteRequest.onerror = (event) => {
      reject((event as any).target.error);
    };

    deleteRequest.onsuccess = () => {
      initializeDB()
        .then(() => {
          resolve();
        })
        .catch((error) => {
          reject(error);
        });
    };
  });
};
