// recoil/atoms.ts

import { atom } from "recoil";

export interface Memo {
  id: number;
  content: string;
}

export const memoListState = atom<Memo[]>({
  key: "memoListState",
  default: [],
});

export const selectedMemoState = atom<Memo | null>({
  key: "selectedMemoState",
  default: null,
});
